$font-stack: Helvetica, sans-serif;
$primary-color: #333;

// COMMON //

.back {
  background-color: lightblue;
  width: 390px;
  height: 844px;
  background-image: repeating-linear-gradient(
    45deg,
    rgb(120, 165, 179) 0px,
    rgb(84, 137, 155) 40px
  );
  position: absolute;
}

body {
  font: 100% $font-stack;
  color: $primary-color;
  font-size: 2rem;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}

.center-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

// STATE //

.selected {
  color: red;
}

.initial {
  color: black;
}


// ROLL BUTTON //

.roll-button-base {
  border-radius: 100%;
  background: linear-gradient(
    45deg,
    rgb(226, 207, 127) 0%,
    rgb(120, 96, 42) 50%,
    rgb(212, 127, 45) 100%
  );

  position: relative;
  width: 200px;
  height: 200px;
  border: solid 1px black;
  background-color: white;
  mix-blend-mode: hard-light;
  display: flex;
  justify-content: center;
  align-items: center;

  backdrop-filter: blur(3px);
  box-shadow: 0 0 30px -5px rgba(0, 0, 0, 100);
  
  bottom: 10px;
}

.roll-button-hole {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 160px;
  width: 160px;

  border: solid 1px red;
  border-radius: 50%;
  background-color: gray;

  color: rgb(199, 187, 158);
  font-size: 1em;
  font-family: "Lucida Console", Courier, monospace;
}

.roll-button-hole:hover {
  transform: scale(1.1) rotate(1deg);
  transform-origin: center center;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.25), 0 0 48px rgba(0, 0, 0, 0.35);
}

.roll-button-hole:active {
  transform: scale(1) rotate(-1deg);
  transform-origin: center center;
}

// DICE //

.dice-wrapper {
  position: relative;
}